<template>
<div>
<a-row>
  <a-col span="5"> </a-col>
  <a-col span="12" class="title">
    Survival Probability Calculator
  </a-col>
  <a-col span="5"> </a-col>
</a-row>
  <a-row v-if="load">
    <a-col span="5"> </a-col>
    <a-col span="8">
      <a-form>
        <a-form-item label="Survival time since surgery">
          <a-select v-model="survprob" style="width: 70%; min-width: 100px">
            <a-select-option
              v-for="month in months"
              :key="month"
              :value="month"
            >
              {{ month }} months
            </a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          v-for="(item, index) in point_scores"
          :key="item.type"
          :label="item.type"
        >
          <a-radio-group v-model="scores[index]">
            <a-radio-button
              v-for="point in item.points"
              :key="point.label"
              :value="point.value"
            >
              {{ point.label }}
            </a-radio-button>
          </a-radio-group>
        </a-form-item>
      </a-form>
    </a-col>
    <a-col span="6">
      <div class="info-area">
      <div>1-year survival probability: <span>{{ showProb(1) }}</span></div>
      <div>3-year survival probability: <span>{{ showProb(3) }}</span></div>
      <div>5-year survival probability: <span>{{ showProb(5) }}</span></div>
      </div>
      <div>
        <chart :value="probs"></chart>
      </div>
    </a-col>
    <a-col span="5"> </a-col>
  </a-row>

</div>

</template>

<script>
import chart from "./components/chart";
export default {
  components: { chart },
  data() {
    return {
      point_scores: [],
      scores: [],
      survprob: 0,
      cals: [[], [], [], [], []],
      load: false,
      limits: [0, 93, 81, 69, 57, 45],
      mychart: null,
    };
  },
  created: function () {
    console.log("created");
    var inits = [this.initScores()];
    for (var i = 1; i <= 5; i++) {
      inits.push(this.initCal(i));
    }
    var that = this;
    Promise.all(inits).then(function () {
      console.log("init");
      var probs = [];
      for (var i = 1; i <= 5; i++) {
        probs.push(that.caculate(i));
      }
      that.probs = probs;
      that.load = true;
    });
  },
  watch: {
    scores: function () {
      if (!this.load) return;
      this.updateProbs();
    },
    survprob: function () {
      if (!this.load) return;
      this.updateProbs();
    },
  },
  methods: {
    initScores: function () {
      var that = this;
      return new Promise(function (resolve, reject) {
        that.$axios.get("../static/data/pointscore.json").then(function (res) {
          that.point_scores = res.data;
          for (var i = 0; i < that.point_scores.length; i++) {
            var p = that.point_scores[i];
            that.scores.push(p.points[0].value);
          }
          resolve();
        });
      });
    },
    initCal: function (y) {
      var that = this;
      return new Promise(function (resolve, reject) {
        that.$axios
          .get("../static/data/" + y + "ycalculator.json")
          .then((res) => {
            that.cals[y] = res.data;
            resolve();
          });
      });
    },
    showProb: function (y) {
      var prob = this.probs[y - 1];
      return prob == -1 ? "" : prob + "%";
    },
    updateProbs: function () {
      var probs = [];
      for (var i = 1; i <= 5; i++) {
        probs.push(this.caculate(i));
      }
      this.probs = probs;
    },
    caculate: function (y) {
      if (this.survprob > this.limits[y]) {
        return -1;
      }
      var sum = 0;
      for (var i = 0; i < this.scores.length; i++) {
        sum += this.scores[i];
      }
      var survprob = "survprob" + this.survprob;
      var k = 0;
      for (; k < this.cals[y].length; k++) {
        if (this.cals[y][k].points >= sum) break;
      }
      if (
        k > 0 &&
        sum - this.cals[y][k - 1].points < this.cals[y][k].points - sum
      ) {
        k -= 1;
      }
      var prob = this.cals[y][k][survprob];
      prob *= 100;
      prob = Math.round(prob * 100) / 100;
      return prob;
    },
  },
  computed: {
    months: function () {
      var lst = [];
      for (var i = 0; i <= 93; i += 3) {
        lst.push(i);
      }
      return lst;
    },
  },
};
</script>

<style>
.ant-form-item{
  margin-bottom: 5px;
}
.title{
  font-size: 34px;
  font-weight: 500;
  color: #5470c6;
  margin: 30px 0;
}
.info-area{
  /* color: rgba(0,0,0,0.85); */
  font-size: 16px;
  font-weight: 500;
  margin-top: 30px;
}
.info-area span{
  color: #5470c6;
}
</style>


