<template>
  <div id="chart" style="width: 100%; height: 400px"></div>
</template>

<script>
export default {
  name: "chart",
  props: ["value"],
  data() {
    return {
      mychart: null,
    };
  },
  mounted: function () {
    var chartDom = document.getElementById("chart");
    this.myChart = this.$echarts.init(chartDom);
    this.option && this.myChart.setOption(this.option);
  },
  watch: {
    value: function () {
      this.option && this.myChart.setOption(this.option);
    },
  },
  computed: {
    option: function () {
      var xs = [];
      var ys = [];
      for(var i=0;i<this.value.length;i++){
        if(this.value[i]==-1)continue;
        xs.push(i+1);
        ys.push(this.value[i]);
      }
      return {
        xAxis: {
          type: "category",
          data: xs,
          name: "year",
        },
        yAxis: {
          type: "value",
          name: "survival probability",
          nameTextStyle : {padding: [4,4,4,50]},
          min: 0,
          max: 100
        },
        series: [
          {
            data: ys,
            type: "line",
            smooth: true,
            label:{
              show: true,
              formatter: '{c}%'
            }
          },
        ],
      };
    },
  },
};
</script>


